var options = [];
export default {
	data() {
		return {
			moren: true,
			region: [],
			moren: false,
			province: '',
			city: '',
			area: '',
			address: '',
		}
	},
	onLoad(options) {
		var that = this;
		that.options = options;
		console.log(options);
		if (options.address_id) {
			console.log('有ID')
			this.$http.post("address/getAddress", {
				address_id: options.address_id
			}).then((res) => {
				console.log('获取收货地址')
				that.address = res.data;
				that.moren = res.data.status == 2 ? true : false;
			}).catch(err => {
				console.log('返回错误信息', err);
			})
		}
	},
	methods: {
		bindRegionChange(e) {
			console.log('地址===', e);
			this.region = e.detail.value;
			this.province = e.detail.value[0];
			this.city = e.detail.value[1];
			this.area = e.detail.value[2];
		},
		switch1Change(e) {
			console.log(e.detail.value) // true为选中false没选中
			this.moren = e.detail.value;
		},
		// 提交地址
		tijiao(e) {
			uni.removeStorageSync('address_id');
			var that = this;
			this.addFormID(e.detail.formId)
			var name = encodeURI(e.detail.value.name);
			var phone = e.detail.value.phone;
			var province = encodeURI(that.province);
			var city = encodeURI(that.city);
			var area = encodeURI(that.area);
			var address = encodeURI(e.detail.value.address);

			if (name.length == 0 || phone.length == 0 || province.length == 0 || city.length == 0 || area.length == 0 ||
				address.length == 0) {
				uni.showModal({
					title: '温馨提示',
					content: '请完善收货地址！',
					showCancel: false
				})
				return false
			}

			if (that.options.address_id) {
				// 修改
				this.$http.post("address/updAddress", {
					phone: phone,
					address_id: that.options.address_id
				}, {
					headers: {
						'content-type': 'application/x-www-form-urlencoded',
						'name': name,
						'province': province,
						'city': city,
						'area': area,
						'address': address,
						'moren': that.moren,
					},
				}).then((res) => {
					uni.showToast({
						title: '修改成功',
						success: function() {
							setTimeout(function() {
								uni.navigateBack({})
							}, 1500);
						}
					})
				}).catch(err => {
					console.log("错误信息==", err);
				})

			} else {
				this.$http.post("address/addAddress", {
					phone: phone
				}, {
					headers: {
						'content-type': 'application/x-www-form-urlencoded',
						'name': name,
						'province': province,
						'city': city,
						'area': area,
						'address': address,
						'moren': that.moren,
					},
				}).then((res) => {
					uni.showModal({
						title: '温馨提示',
						content: res.data,
						showCancel: false,
						success: function() {
							uni.navigateBack({
								delta: 1
							})
						}
					})
				})
			}

		},

		deladdress(e) {
			var that = this;
			console.log('获取地址ID', e)
			var address_id = e.currentTarget.dataset.address_id;
			uni.showModal({
				title: '温馨提示',
				content: '确认要删除该记录吗？',
				success: function(res) {
					console.log(res);
					if (res.confirm == true) {
						that.$http.post("address/delAddress", {
							address_id: address_id
						}).then((res) => {
							uni.showToast({
								title: '操作成功',
								success: function() {
									setTimeout(function() {
										uni.navigateBack({})
									}, 1500)
								}
							})
						})
					}
				}
			})

		},

	}
}
